@use "sass:math";

$max-width: 1200px;
$font-family: montserrat, Trebuchet MS, Verdana, Geneva, sans-serif;
$mobile-breakpoint: 768px;
$small-mobile-breakpoint: 380px;
$a4-ratio: math.div(70, 99);

$primary-color: #3D4D60;
$secondary-color: #2D3D50;
$tertiary-color: #98BEC8;

$accent-color-lightest: #FFFFFF;
$accent-color-lighter: #EEEEEE;
$accent-color-light: #DDDDDD;
$accent-color: #DDCCCC;
$accent-color-secondary: #AAAAAA;
$accent-color-tertiary: #AA7777;
$accent-color-dark: #AA9999;
$accent-color-darker: #3D3333;
$accent-color-darkest: #221111;

@mixin text_page_padding() {
    padding: 0rem 1rem;
}

@mixin box_shadow($color) {
    border: 1px solid $color;
    box-shadow: $color 3px 3px;
}

@mixin wide_card($background, $shadow) {
    @if ($background) { @include background_noise($background); }
    @if ($shadow) { @include box_shadow($shadow); }
    margin: 0rem 0rem 1rem;
    padding: 0.5rem 1rem 0.5rem;
}

@mixin interactive($gutter, $hover-bg, $hover-color, $active-bg, $active-color) {
    cursor: pointer;
    
    @media (hover: hover) {
        &:hover::after {
            content: '';
            position: absolute;
            width: calc(100% - ($gutter * 2));
            height: calc(100% - ($gutter * 2));
            top: $gutter;
            right: $gutter;
            bottom: $gutter;
            left: $gutter;
            opacity: 0.2;
            border-radius: 0.2rem;
            background: $hover-bg;
            color: $hover-color;
        }
        
        &:active::after {
            content: '';
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            opacity: 0.6;
            border-radius: 0;
            background: $active-bg;
            color: $active-color;
        }
    }
}

@mixin background_noise($color) {
    position: relative;
    background-color: $color;
    z-index: 1;
    &:before {
        z-index: -1;
        content: "";
        position: absolute;
        top: 0px;
        right: 0px;
        bottom: 0px;
        left: 0px;
        opacity: 0.75;
        background-size: 60px 60px;
        background-image: $noise-texture;
    }
}

